.sci-fi-card {
  position: relative;
  background: rgba(0, 0, 0, 0.6);
  border: 1px solid rgba(0, 255, 255, 0.2);
  border-radius: 1rem;
  padding: 2rem;
  backdrop-filter: blur(10px);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}
.sci-fi-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(0, 255, 255, 0.1),
    transparent
  );
  transition: left 0.5s;
}
.sci-fi-card:hover::before {
  left: 100%;
}
.sci-fi-card:hover {
  border-color: rgba(0, 255, 255, 0.5);
  box-shadow: 0 0 30px rgba(0, 255, 255, 0.2);
}
.sci-fi-card-content {
  position: relative;
  z-index: 10;
}
.sci-fi-card-glow {
  position: absolute;
  inset: -2px;
  background: radial-gradient(circle at var(--mouse-x, 50%) var(--mouse-y, 50%), rgba(0, 255, 255, 0.1), transparent 70%);
  opacity: 0;
  transition: opacity 0.3s;
  pointer-events: none;
  border-radius: 1rem;
}
.sci-fi-card:hover .sci-fi-card-glow {
  opacity: 1;
}
.cta-button-sci-fi-primary {
  position: relative;
  padding: 1.25rem 2.5rem;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid var(--brand-gold);
  background: linear-gradient(135deg, rgba(251, 191, 36, 0.1), rgba(251, 191, 36, 0.05));
  color: var(--brand-gold);
  border-radius: 0;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-family: var(--font-display);
  cursor: pointer;
}
.cta-button-sci-fi-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gradient-gold);
  transition: left 0.4s;
  z-index: -1;
}
.cta-button-sci-fi-primary:hover {
  color: #000;
  border-color: var(--brand-gold);
  box-shadow: 0 0 30px rgba(251, 191, 36, 0.5);
  transform: translateY(-2px);
}
.cta-button-sci-fi-primary:hover::before {
  left: 0;
}
.cta-button-sci-fi-secondary {
  position: relative;
  padding: 1.25rem 2.5rem;
  font-size: 1rem;
  font-weight: 600;
  border: 2px solid var(--accent-cyan);
  background: transparent;
  color: var(--accent-cyan);
  border-radius: 0;
  overflow: hidden;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-family: var(--font-display);
  cursor: pointer;
}
.cta-button-sci-fi-secondary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: var(--gradient-cyan);
  transition: left 0.4s;
  z-index: -1;
}
.cta-button-sci-fi-secondary:hover {
  color: #000;
  border-color: var(--accent-cyan);
  box-shadow: 0 0 30px rgba(0, 255, 255, 0.5);
  transform: translateY(-2px);
}
.cta-button-sci-fi-secondary:hover::before {
  left: 0;
}
.cta-shine {
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(
    90deg,
    transparent,
    rgba(255, 255, 255, 0.3),
    transparent
  );
  transition: left 0.5s;
}
.cta-button-sci-fi-primary:hover .cta-shine,
.cta-button-sci-fi-secondary:hover .cta-shine {
  left: 100%;
}
.cta-icon {
  display: inline-block;
  margin-left: 0.5rem;
  transition: transform 0.3s;
}
.cta-button-sci-fi-primary:hover .cta-icon,
.cta-button-sci-fi-secondary:hover .cta-icon {
  transform: translateX(4px);
}
@media (max-width: 768px) {
  .cta-button-sci-fi-primary,
  .cta-button-sci-fi-secondary {
    padding: 1rem 1.5rem;
    font-size: 0.875rem;
